.copied-style-sp {
  position: absolute;
  font-family: PingFang SC;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 17px;
  text-align: center;
  color: #ffffff;
  background: #0f134f;
  border-radius: 34px;
  z-index: 9000;
  padding: 7px 22px;
  white-space: nowrap;
  cursor: pointer;
  bottom: 30px;
  left: 0;
  right: 0;
  margin: auto;
  width: 85px;
}

