.sort-select {
  position: relative;
  min-width: 100px;
  &.en {
    min-width: 124px;
  }
  // background: rgba(15, 19, 79, 0.1);
  border-radius: 2px;
  .ant-select-selector {
    height: 20px !important;
    padding-left: 28px !important;
    padding-right: 26px !important;
    background: rgba(15, 19, 79, 0.1) !important;
    border-radius: 2px !important;
    border: none !important;
  }
  .ant-select-selection-search {
    .ant-select-selection-search-input {
      display: none !important;
    }
  }
  .ant-select-selection-item {
    line-height: 20px !important;
    padding-right: 0 !important;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #0f134f;
    text-overflow: unset;
  }
  .ant-select-arrow {
    margin-top: -3px;
    right: 9px;
  }
  .ant-select-suffix {
    width: 10px;
    height: 6px;
    background: url(../images/mobile/arrow-down-black.svg) 0 0 no-repeat;
    background-size: 100%;
    > svg {
      display: none;
    }
  }
  &::after {
    content: '';
    display: block;
    width: 11px;
    height: 9px;
    background: url(../images/mobile/sort-icon.svg) 0 0 no-repeat;
    background-size: 100%;
    position: absolute;
    top: 6px;
    left: 10px;
  }
  &.ant-select-open {
    background: rgba(61, 86, 214, 0.1);
    .ant-select-selection-item {
      color: #3d56d6 !important;
    }
    .ant-select-suffix {
      background-image: url(../images/mobile/arrow-down-blue.svg);
    }
    &::after {
      background-image: url(../images/mobile/sort-active-icon.svg);
    }
  }
  &.desc {
    &::after {
      background-image: url(../images/mobile/sort-desc-icon.svg);
    }
    &.ant-select-open {
      &::after {
        background-image: url(../images/mobile/sort-desc-active-icon.svg);
      }
    }
  }
  &.asc {
    &::after {
      background-image: url(../images/mobile/sort-asc-icon.svg);
    }
    &.ant-select-open {
      &::after {
        background-image: url(../images/mobile/sort-asc-active-icon.svg);
      }
    }
  }
}

.sort-select-down {
  padding: 0 !important;
  border-radius: 6px !important;
  margin-top: 42px !important;
  overflow: visible !important;
  min-width: 160px !important;
  width: auto !important;
  &::after {
    content: '';
    display: block;
    width: 17.21px;
    height: 4px;
    background: url(../images/mobile/triangle-top-white.png) 0 0 no-repeat;
    background-size: 100%;
    position: absolute;
    top: -3px;
    left: 30%;
    transform: translateX(-50%);
  }
  .ant-select-item-option-content {
    text-align: center;
  }
  .ant-select-item-option-active,
  .ant-select-item-option-selected {
    background: rgba(61, 86, 214, 0.06) !important;
    color: #3d56d6 !important;
  }
  .ant-select-item {
    align-items: center;
    margin: 0 5px !important;
    border-radius: 4px;
    padding: 0;
    &:first-child {
      margin-top: 8px !important;
    }
    &:last-child {
      margin-bottom: 8px !important;
    }
  }
  .div-item {
    padding: 8px !important;
    text-align: left;
    &.active {
      background: transparent !important;
      color: #3d56d6 !important;
      font-weight: 400 !important;
      .long-arrow {
        background-image: url(../images/mobile/arrow-long-active-icon.svg);
      }
    }
  }
  .long-arrow {
    display: inline-block;
    width: 14px;
    height: 10px;
    background: url(../images/mobile/arrow-long-icon.svg) 0 0 no-repeat;
    background-size: 100%;
    margin: 0 4px;
  }
}
