  .wait {
    // width: 360px;
    height: 39px;
    background: rgba(61, 86, 214, 0.06);
    border-radius: 6px;
    display: flex;
    padding: 0 20px;
    justify-content: space-between;
    margin-bottom: 10px;
    span {
      font-weight: 400;
      font-size: 12px;
      line-height: 39px;
      color: #0f134f;
      &:nth-child(2) {
        font-family: 'Rubik-Medium';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #3d56d6;
      }
    }
  }
  .rewards-box {
    background: rgba(61, 86, 214, 0.06);
    // width: 360px;
    height: 172px;
    border-radius: 6px;
    padding: 10px;
    margin-bottom: 10px;
    .rewards-title {
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: #0f134f;
      height: 17px;
      margin-bottom: 10px;
      text-indent: 10px;
    }
    .content {
      // width: 340px;
      height: 125px;
      background: #ffffff;
      border-radius: 8px;
      padding-top: 10px;
    }
    .column {
      width: 100%;
      height: 17px;
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: #545669;
      margin-bottom: 10px;
      display: flex;
      padding: 0 10px;
      justify-content: space-between;
      span {
        font-family: 'PingFang SC';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        height: 17px;
        color: #545669;
      }

      img {
        display: inline-block;
        width: 21px;
        height: 15px;
        vertical-align: top;
        margin-left: 6px;
      }
    }
    .line {
      opacity: 0.05;
      border: 1px solid #0f134f;
      margin-bottom: 8px;
    }
  }
  .tips {
    background: url(../images/modalWarning.svg) no-repeat top 2px left;
    padding-left: 18px;
    p {
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: #ff8718;
      margin: 0;
    }
  }

  .empty {
    width: 100%;
    height: 180px;
    padding: 60px;
    img {
      width: 80px;
      height: 60px;
      margin: 0 auto;
      display: block;
    }
    p {
      width: 100%;
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      text-align: center;
      color: #545669;
      height: 17px;
      margin-top: -40px;
    }
  }
}

