  font-family: 'DM Sans' !important;
  > span {
    color: #0f134f !important;
    font-family: 'Rubik-Medium' !important;
    font-size: 14px !important;
  }
}

.mortgage-modal {
  .ant-modal-header {
    padding: 20px 24px;
  }
  .mortgage-modal-body {
    line-height: 24px;
    font-size: 14px;
    font-family: PingFang SC;
    color: #0f134f;
    text-align: left;
  }
  .collateral_btn {
    width: 90px;
    height: 34px;
    line-height: 34px;
    margin-top: 20px;
    font-size: 14px;
    color: #fff;
    text-align: center;
    border: none;
    border-radius: 8px;
    background: #3d56d6;
  }
}

.trans-modal {
  .ant-modal-body {
    padding: 0 0 20px 0;
  }
  .trans-modal-title {
    border-bottom: 1px solid rgba(15, 19, 79, 0.05);
  }
  .trans-modal-icon {
    margin-top: 30px !important;

    .authorizeIcon {
      width: 50px;
      height: 50px;
    }
  }
  .modal-btn {
    width: auto;
    margin: 20px 20px 0;
  }
}

.modal-btn {
  width: 100%;
  height: 40px;
  line-height: 40px;
  padding: 0;
  font-size: 14px;
  color: #fff;
  text-align: center;
  border: none;
  border-radius: 8px;

  &.lend-btn {
    background-color: #1ed0ac;
  }

  &.repay-btn {
    background-color: #ff8718;
  }

  &.authorize-btn,
  &.deposit-btn {
    background-color: #3d56d6;
  }

  &.withdraw-btn {
    background-color: #773dd6;
  }

  &.border-btn {
    color: #3d56d6;
    background-color: #fff;
    border: 1px solid #3d56d6;

    &:hover {
      color: #3d56d6;
    }
  }

  &.no-use-btn {
    color: #84869e;
    background-color: #f0f1f6;

    &:hover {
      color: #84869e;
    }
  }

  &:hover {
    color: #fff;
  }

  &:disabled,
  &:disabled:hover {
    background: #f0f1f6;
    color: #84869e;
    cursor: not-allowed;
  }
}

.common-input {
  input::-webkit-input-placeholder {
    font-weight: 400;
  }
  input {
    color: #545669;
  }
}

.new-switch {
  display: inline-block;
  position: relative;
  width: 32px;
  height: 16px;
  > span {
    display: inline-block;
  }
  .switch-line {
    position: absolute;
    top: 2px;
    left: 0;
    width: 32px;
    height: 12px;
    background: #d1d7e9;
    border-radius: 10px;
  }
  .switch-circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 16px;
    height: 16px;
    background: #ffffff;
    box-shadow: 0px 0px 5px rgba(99, 97, 97, 0.2);
    border-radius: 50%;
    transition: all 0.5s ease-in-out;
  }
  &.switch-on {
    .switch-line {
      background: #3d56d6;
    }
    .switch-circle {
      left: auto;
      right: 0;
    }
  }
}

